import {createApi, fetchBaseQuery} from '@reduxjs/toolkit/query/react'

export const dataApi = createApi({
    reducerPath: 'dataApi',
   baseQuery: fetchBaseQuery({baseUrl: 'https://api.github.com/users/culcat'}),
   endpoints:(build) => ({
    getData: build.query({
        query:()=>'/repos',
    })
})
})

export const {useGetDataQuery} = dataApi